	text: ['Основанная в 1958 году, Project HOPE является известной всемирной организацией здравоохранения и гуманитарной помощи. Миссия организации: передать власть в руки местных медицинских работников, чтобы спасти жизнь по всему миру. Проект HOPE работает в пяти основных областях: катастрофы и кризисы в сфере здравоохранения; инфекционные заболевания; неинфекционные заболевания; здоровье матери, новорожденного и ребенка; политика здравоохранения. В катастрофах, кризисах и за их пределами HOPE здесь: предоставляет прямые медицинские услуги, оборудует клиники и больницы, обучает местных медицинских работников и предлагает решения, которые помогают людям получить доступ к услугам здравоохранения, которые им нужны.'],
