//mix card
for (let lang in content) {
	for (let action in content[lang].cards) {
		for (let country in content[lang].cards[action]) {
			content[lang].cards[action][country] = content[lang].cards[action][country].sort(()=>Math.random()-0.5);
		}
	}
}

export { content };


