import style from './index.module.css';
import { content } from './content.js';
import { content as homeContent } from '../home/content.js'
import { useEffect } from 'react';
import { useLocation } from "react-router-dom";
import TextBox from "../components/textBox";
import Recommended from "../components/recommendedCards";
import ContactBox from "../components/contactBox";


function ContactUs(props) {
	const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

	const lang = content[props.lang];
	const emptyContact = homeContent[props.lang].emptyContact;


	const contacts = {
		site: 'nolra.github.io/donationsvsevil',
		facebook: 'donationsVSevil',
		instagram: 'donationsvsevil',
		twitter: 'donationsVSevil',
		telegram: 'donationsVSevil',
		mail: 'donatevsevil@riseup.net',
	}

  return (
	  <section className={style.content}>
		  	<div className={style.hero}>
		  		<h1>{lang.header}</h1>
		  		<div className={style.tags}>
		  			<div>{lang.suggestions}</div>
		  			<div className={style.country}><img src={require(`../images/icons/anarchy.png`)} alt={`anarchy`} /></div>
		  			<div>{lang.support}</div>
		  		</div>
		  	</div>
		  	<div className={style.main}>
		  		<div className={style.text}>
		  			<TextBox header={lang.main.header} text={lang.main.text}/>
		  			<ContactBox header={lang.header} emptyContact={emptyContact} contacts={contacts} />
		  		</div>
		  		<Recommended lang={props.lang} />
		  	</div>
	  </section>
  );
}

export default ContactUs;





