import style from "./index.module.css";
import { content } from "./content.js";
import { Link } from "react-router-dom";
import { useParams } from "react-router";


function ToSearch(props) {
  const host = window.env.ENV_HOST;
	const text = content[props.lang].text;
	const top = useParams().top;
	return(
		top ? <Link to={host} className={style.toSearch}>{text}</Link> 
				: <Link to={`${host}org`} className={style.toSearch}>{text}</Link> 
	)
}

export default ToSearch;
