import style from './index.module.css';
import { content } from './content.js'
import { uniqueId, find } from '../functions'
import { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from "react-router-dom";
import CardsWrapper from "../components/cardWrapper";

function Top(props) {

	// NAVIGATION
	const host = window.env.ENV_HOST;
  const { pathname } = useLocation();
  const reloadPage = useLocation().search
  const navigate = useNavigate();
  useEffect(() => {
	  if (reloadPage !== "") {
	  	const to = reloadPage.slice(4);
	  	navigate(`${host}${to}`);
	  }
    window.scrollTo(0, 0);
  }, [pathname]);

  // STATES
	//filters content

	const [openAction, setOpenAction] = useState(false);
	const country = props.country;
	const action = props.action;
	const setAction = props.setAction;
	const [searchString, setSearchString] = useState('');
	//lang content
	const lang = content[props.lang]
	//cards content
	const defaultCards = lang.cards[action][country];
	const [cards, setCards] = useState(lang.cards[action][country]);



	// EFFECTS
	//cards search
  useEffect(() => {
		setCards(lang.cards[action][country]);
		setSearchString('');
  }, [action, country, lang]);

  // SEARCH LOGIC
	function search(event) {
		event.preventDefault();
		if (event.target.value !== '') {
			const filterCards = []; 
			defaultCards.forEach( (card) => {
				const header = card.header.toLowerCase();
				const desc = card.desc.toLowerCase();
				const string = event.target.value.toLowerCase();
				if (header.includes(string) || desc.includes(string)) filterCards.push(card)
			});
			setCards(filterCards);
		} else {
			setCards(defaultCards);
		}
		setSearchString(event.value);
	}
	function closeFilters() {
		setOpenAction(false);
	}

	// LISTS RENDER

	const actionList = function() {
		let actionJSX = [];
		for (let listAction in lang.filterAction) {
			if (listAction !== action) {
				actionJSX.push(
		  		<div key={listAction} onClick={() => setAction(listAction)}>
		  			{lang.filterAction[listAction]}
		  		</div>
				)
			}
		}
		return actionJSX;
	};

  return (
	  <main>
	  	<section className={style.hero}>
	  		<div className={style.headSide}>
	  			<h1>{lang.header[0]} <br/> {lang.header[1]} <span>{lang.header[2]}</span></h1>
	  		</div>
	  		<div className={style.textSide}>
	  			<p>{lang.desc}</p>
	  			<Link to={`${host}about`} className={style.button}>{lang.buttonMore}</Link>
	  		</div>
	  	</section>

	  	<section className={style.filters}>
	  		<div onClick={() => setOpenAction(!openAction)} className={`${style.filter} ${openAction ? style.active : ''}`}>
	  			{lang.filterAction[action]}
	  			<div className={style.filterList}>
	  				{actionList()}
	  			</div>
	  		</div>
  			<div className={style.search}>
  				<input onFocus={closeFilters} value={searchString} placeholder={lang.search} type="text" onChange={search}/>
  			</div>
	  	</section>
	  	<CardsWrapper
	  		top={true}
	  		empty={lang.empty}
	  		cards={cards}
	  		report={lang.report}
	  		more={lang.buttonMore}
	  		contacts={lang.buttonContacts}
	  		action={action}
	  		country={country}
	  		header={lang.topHeader}
	  		lang = {lang}
	  	/>
	  </main>
  );
}

export default Top;
