import style from "./index.module.css";

function ContactBox(props) {
	const header = props.header;
	const contacts = props.contacts;
	const emptyContact = props.emptyContact;
	
	return(
		<>
			<h2 className={style.header}>{header}</h2>
			<div className={style.contacts}>
				<div className={style.box}>
					{
						contacts.mail ? (
							<a className={style.mail} href={`mailto:${contacts.mail}`}>{contacts.mail}</a>
						) : (
							<span className={style.mail}>{emptyContact}</span>
						)
					}
					{
						contacts.site ? (
							<a className={style.site} target="_blank" href={`${contacts.http ? 'http://' : 'https://'}${contacts.site}`}>{contacts.site}</a>
						) : (
							<span className={style.site}>{emptyContact}</span>
						)
					}
					{
						contacts.facebook ? (
							<a className={style.facebook} target="_blank" href={`https://facebook.com/${contacts.facebook}`}>{contacts.facebook}</a>
						) : (
							<span className={style.facebook}>{emptyContact}</span>
						)
					}
				</div>
				<div className={style.box}>
					{
						contacts.instagram ? (
							<a className={style.instagram} target="_blank" href={`https://www.instagram.com/${contacts.instagram}`}>{contacts.instagram}</a>
						) : (
							<span className={style.instagram}>{emptyContact}</span>
						)
					}
					{
						contacts.telegram ? (
							<a className={style.telegram} target="_blank" href={`https://t.me/${contacts.telegram}`}>{contacts.telegram}</a>
						) : (
							<span className={style.telegram}>{emptyContact}</span>
						)
					}
					{
						contacts.twitter ? (
							<a className={style.twitter} target="_blank" href={`https://twitter.com/${contacts.twitter}`}>{contacts.twitter}</a>
						) : (
							<span className={style.twitter}>{emptyContact}</span>
						)
					}
				</div>
			</div>
  	</>
	)
}

export default ContactBox;
