import style from "./index.module.css";
import { useState } from 'react';
import { content } from "./content.js";
import { Link } from "react-router-dom";

function ModalFAQ(props) {
	const header = content[props.lang].header;
  const text = content[props.lang].text;
  const link = content[props.lang].link;
  const [ show, setShow] = useState(true);
  
  const host = window.env.ENV_HOST;

  function closeModal() {
  	localStorage.setItem('showModal', true);
  	setShow(false);
  }

	return(
		<>
			{
				show && (
					<>
						<div className={style.modal}>
							<div onClick={closeModal} className={style.close}></div>
							<h2>{header}</h2>
							<p>
								{`${text} `}
								<Link to={`${host}faq#howToSupportSafely`} className={style.link}>{link}</Link>
							</p>
						</div>
						<div className={style.modalBg}></div>
					</>
				)
			}

		</>
	)
}


export default ModalFAQ;
