import style from './index.module.css';
import TextBox from "../components/textBox";
import ToTop from "../components/toTop";
import { content } from './content.js'
import { Link, animateScroll } from 'react-scroll'
import { useEffect } from 'react';
import { useLocation } from "react-router-dom";

function FAQ(props) {
	const { pathname, hash } = useLocation();
  useEffect(() => {
  	if (hash !== '' && hash) {
  		if (hash === '#howToSupportSafely') localStorage.setItem('showModal', true);
  		const position = document.querySelector(hash).offsetTop - 20;
			animateScroll.scrollTo(position, {
			  duration: 500,
			  smooth: true,
			  spy: true,
			})
  	} else {
  		window.scrollTo(0, 0);
  	}
  }, [pathname]);
	const lang = content[props.lang]

  return (
	  <section className={style.content}>
		  	<div className={style.hero}>
		  		<h1>{lang.header}</h1>
		  		<div className={style.tags}>
		  			<div>{lang.questions}</div>
		  			<div className={style.country}><img src={require(`../images/icons/anarchy.png`)} alt={`anarchy`} /></div>
		  			<div>{lang.answers}</div>
		  		</div>
		  	</div>

		  	<div className={style.main}>
		  		<div id='content' className={style.donate}>
		  			<div className={style.box}>
					  	<ul className={style.questions}>
					  		{
					  			lang.faq.map((faq) => (
										<li key={faq.question.id}>
									    <Link to={faq.question.id} offset={-20} spy={true} smooth={true} duration={500}>
									    	{faq.question.text}
									    </Link>
										</li>							  		
					  			))
					  		}
					  	</ul>
		  			</div>
		  			<div className={style.gallery}>
		  				<div className={style.galleryHeader}>{lang.gallery.header}</div>
		  				{
		  					lang.gallery.content.map((galleryItem) => {
		  						return(
			  						<div key={galleryItem.url} className={style.galleryBox}>
			  							<div className={style.galleryImage}>
			  								<img src={require(`../images/gallery/${galleryItem.url}.jpg`)} alt={galleryItem.desc} />
			  							</div>
			  							<div className={style.galleryDesc}>
			  								{galleryItem.desc}
			  							</div>
			  						</div>
		  						)
		  					})
		  				}
		  			</div>
		  		</div>

		  		<div className={style.text}>
			  		{
			  			lang.faq.map((faq) => (
								<div key={faq.question.id} id={faq.question.id}>
									<TextBox header={faq.question.text} text={faq.answer.text}/>
								</div>
			  			))
			  		}
			  	</div>
		  	</div>
		  	<ToTop target='content' lang={props.lang}/>
	  </section>
  );
}

export default FAQ;
