import { Link } from "react-router-dom";
import { useParams } from "react-router";
import style from './index.module.css';

function CardsWrapper(props) {
	const empty = props.empty;
	const cards = props.cards;
	const more = props.more;
	const report = props.report;
	const contacts = props.contacts;
	const country = props.country;
	const action = props.action;
	const header = props.header;
	const top = props.top;
	const pageTop = useParams().top;
	const lang = props.lang;
	const host = window.env.ENV_HOST;

	if (pageTop) {
		return <></>
	}

	return (
  	<div className={style.wrapper}>
	  	<section className={style.cards}>
				{	header && <h1>{header}</h1>	}
	  		{
	  			cards.map((card) => 
	  			<div key={card.id} className={style.card}>
	  				<div className={style.image}>
	  					{
	  						top ? <div className={style.topPromo}>{card.info.open ? lang.open : lang.closed}</div>
	  								: <img src={require(`../../images/logos/${card.id}.png`)} alt={card.header}/>
	  					}
	  				</div>
	  				<div className={style.text}>
	  					<h2>{card.header}</h2>
	  					<p>{card.desc}</p>
	  					<div className={style.btns}>
	  						{
	  							top	&& card.info.open 
	  								? <Link to={`${host}card/top/${country}/${action}/${card.id}`} className={`${style.button} ${style.more}`}>{more}</Link>
	  								: top	&& !card.info.open 
	  								? <div className={`${style.button} ${style.closed}`}>{lang.closed}</div>
	  									: <Link to={`${host}org/card/${country}/${action}/${card.id}`} className={`${style.button} ${style.more}`}>{more}</Link>
	  						}
	  						{
	  							top	&& card.info.report ? <a href={`${card.info.report}`} className={`${style.button} ${style.more}`}>{report}</a> : undefined
	  						}
	  					</div>
	  				</div>
	  			</div>
	  			)
				}
  			<div key={empty.id} className={style.card}>
  				<div className={style.image}>
  					<img src={require(`../../images/logos/${empty.id}.png`)} alt={empty.header}/>
  				</div>
  				<div className={style.text}>
  					<h2>{empty.header}</h2>
  					<p>{empty.desc}</p>
  					<div className={style.btns}>
  						<Link to={`${host}contacts`} className={`${style.button} ${style.more}`}>{contacts}</Link>
  					</div>
  				</div>
  			</div>
	  	</section>
  	</div>
	)
}

export default CardsWrapper;
