import style from './index.module.css';
import { content } from '../home/content.js'
import { content as topContent } from '../top/content.js'
import { useParams } from "react-router";
import { Link, useLocation } from "react-router-dom";
import { useEffect } from 'react';
import ContactBox from "../components/contactBox";
import TextBox from "../components/textBox";
import CardsWrapper from "../components/cardWrapper";
import ToSearch from "../components/toSearch";
import ModalFAQ from "../components/modalFAQ";

function Card(props) {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

	const country = useParams().country;
	const action = useParams().action;
	const id = useParams().id;
	const top = useParams().top;
	const lang = top ? topContent[props.lang]
									 : content[props.lang];

	const cardContent = top ? topContent[props.lang].cards[action][country].find((card) => card.id === id)
													: content[props.lang].cards[action][country].find((card) => card.id === id);
	const cards = [];

	lang.cards[action][country].forEach(function(item, index) {
		if ((cardContent?.id !== item?.id) && (cards.length < 3)) {
			cards.push(item)
		}
	});

	
  const copyTo = (event) => {
  	event.target.parentNode.querySelector(`.${style.donate__text}`).select();
    document.execCommand("copy");
  }

  const src = top ? require(`../images/backgrounds/about-hero.png`)
  								: require(`../images/logos/${cardContent.id}.png`);
  

  return (
  	<>
		  <section className={style.content}>
		  	<div className={style.hero} style={{backgroundImage: `url(${src})`}}>
		  		<h1>{cardContent.header}</h1>
		  		<div className={style.tags}>
		  			<div>
		  			{
		  				top ? lang.ukraine
		  						: lang.filterCountry[country]
		  			}
		  			</div>
		  			<div className={style.country}><img src={require(`../images/icons/${country}.png`)} alt={country} /></div>
		  			<div>{lang.filterAction[action]}</div>
		  		</div>
		  	</div>

		  	<div className={style.main}>
		  		<div className={style.main__text}>
		  			<TextBox header={cardContent.header} text={cardContent.text}/>
		  			<ContactBox header={lang.buttonContacts} emptyContact={lang.emptyContact} contacts={cardContent.info.contacts} />
		  		</div>

		  		<div className={style.main__donate}>
		  			<div className={style.box}>
		  				<h3>{cardContent.info.donate ? lang.isDonate : lang.isSupport}</h3>
		  				<p>{cardContent.info.donate ? lang.isDonateText : lang.isSupportText}</p>
		  			</div>
		  			<div className={style.donate}>
		  				{
		  					cardContent.info.text.map((text) => (
				  				<div key={text.head} className={style.donate__box}>
				  					<span className={style.donate__head}>{text.head}</span>
				  					<span className={style.donate__item}>
				  						<input className={style.donate__text} readOnly value={text.content}/>
				  						<span className={`${style.donate__icon} ${style.copy_icon}`} onClick={(e) => copyTo(e)}></span>
				  					</span>
				  				</div>
		  					))
		  				}
		  				{
		  					cardContent.info.link.map((link) => (
				  				<div key={link.head} className={style.donate__box}>
				  					<span className={style.donate__head}>{link.head}</span>
				  					<span className={style.donate__item}>
				  						<input className={style.donate__text} readOnly value={link.content}/>
				  						<a className={`${style.donate__icon} ${style.link_icon}`} target="_blank" href={`${link.http ? 'http://' : 'https://'}${link.content}`}></a>
				  					</span>
				  				</div>
		  					))
		  				}
		  			</div>
		  		</div>
		  	</div>
		  </section>
	  	<CardsWrapper 
	  		empty={lang.empty}
	  		cards={cards}
	  		more={lang.buttonMore}
	  		contacts={lang.buttonContacts}
	  		country={country}
	  		action={action}
	  		header={lang.similarCards}
	  	/>
	  	<ToSearch lang={props.lang} />
	  	{
	  		!(localStorage.getItem('showModal')) && <ModalFAQ lang={props.lang} />
	  	}
  	</>
  );
}

export default Card;