import style from './index.module.css';
import { content } from './content.js'
import { uniqueId, find } from '../functions'
import { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from "react-router-dom";
import CardsWrapper from "../components/cardWrapper";

function Home(props) {

	// NAVIGATION
	const host = window.env.ENV_HOST;
  const { pathname } = useLocation();
  const reloadPage = useLocation().search
  const navigate = useNavigate();
  useEffect(() => {
	  if (reloadPage !== "") {
	  	const to = reloadPage.slice(4);
	  	navigate(`${host}${to}`);
	  }
    window.scrollTo(0, 0);
  }, [pathname]);

  // STATES
	//filters content
	const [openCountry, setOpenCountry] = useState(false);
	const [openAction, setOpenAction] = useState(false);
	const [openTags, setOpenTags] = useState(false);
	const country = props.country;
	const setCountry = props.setCountry;
	const action = props.action;
	const setAction = props.setAction;
	const [searchString, setSearchString] = useState('');
	const [tags, setTags] = useState(null);
	//lang content
	const lang = content[props.lang]
	//cards content
	const defaultCards = lang.cards[action][country];
	const [cards, setCards] = useState(lang.cards[action][country]);

	// EFFECTS
	//cards search
  useEffect(() => {
		setCards(lang.cards[action][country]);
		setSearchString('');
  }, [action, country, lang]);
  // tags update
  useEffect(() => {
		resetTagList();
  }, [defaultCards, action, country, lang]);
	// filters close effects
  useEffect(() => {
  	if (openCountry) {
  		setOpenAction(false);
  		setOpenTags(false);
  	}
  }, [openCountry]);
  useEffect(() => {
  	if (openAction) {
  		setOpenCountry(false);
  		setOpenTags(false);
  	}
  }, [openAction]);
  useEffect(() => {
  	if (openTags) {
  		setOpenCountry(false);
  		setOpenAction(false);
  	}
  }, [openTags]);

  // SEARCH LOGIC
	function search(event) {
		event.preventDefault();
		if (event.target.value !== '') {
			const filterCards = []; 
			defaultCards.forEach( (card) => {
				const header = card.header.toLowerCase();
				const desc = card.desc.toLowerCase();
				const string = event.target.value.toLowerCase();
				if (header.includes(string) || desc.includes(string)) filterCards.push(card)
			});
			setCards(filterCards);
		} else {
			setCards(defaultCards);
		}
		setSearchString(event.value);
		resetTagList();
	}
	function closeFilters() {
		setOpenCountry(false);
		setOpenAction(false);
		setOpenTags(false);
	}
	// TAGS LOGIC
	function clickTag(id) {
		let tagsChanged = tags.map( tag => {
			if (tag.id === id) {
				tag.checked = !tag.checked;
			}
			return tag;
		})

		const cardsIds = []; 
		const filterCards = []; 
		defaultCards.forEach(card => {
			tagsChanged.forEach(tag => {
				if (tag.checked) {
					card.tags?.forEach(cardTag => {
						if (cardTag.id === tag.id) {
							if (!filterCards.length || !find(cardsIds,card.id)) {
								cardsIds.push(card.id);
								filterCards.push(card);
							}
						}
					})
				}
			})
		})
		if (filterCards.length) {
			setCards(filterCards);
		} else {
			setCards(defaultCards);
		}
		setTags(tagsChanged);
		setSearchString('');
	}
	function clickTagFilter(event) {
		if (event.target.className.includes('tags')) {
			setOpenTags(!openTags)
		}
	}
	function resetTagList() {
		let tagsCard = defaultCards.map(card => card.tags);
		let tagList = [];
		tagsCard.forEach((tag,index) => {
			if (tag) {
				tag.forEach(tagObj => {
					tagObj.checked=false;
					tagList.push(tagObj);
				})
			}
		});
		setTags(tagList);
	}
	function calcCheckedTags() {
		let calc = 0;
		const uniqueTags = uniqueId(tags);
		uniqueTags.forEach(tag => {
			if (tag.checked) {
				calc++
			}
		})
		return calc;
	}

	// LISTS RENDER
	const filterList = function() {
		let filtersJSX = [];
		for (let listCountry in lang.filterCountry) {
			if (listCountry !== country) {
				filtersJSX.push(
		  		<div key={listCountry} onClick={() => setCountry(listCountry)}>
		  			{lang.filterCountry[listCountry]}
		  			<img src={require(`../images/icons/${listCountry}.png`)} alt={listCountry} />
		  		</div>
				)
			}
		}
		return filtersJSX;
	};

	const actionList = function() {
		let actionJSX = [];
		for (let listAction in lang.filterAction) {
			if (listAction !== action) {
				actionJSX.push(
		  		<div key={listAction} onClick={() => setAction(listAction)}>
		  			{lang.filterAction[listAction]}
		  		</div>
				)
			}
		}
		return actionJSX;
	};

	const tagsList = function() {
		const uniqueTags = uniqueId(tags);
		let tagsJSX = uniqueTags.map(tag => {
			return	(
	  		<div key={tag.id} className={tag.checked ? style.checked : ''} onClick={() => clickTag(tag.id)}>
	  			{tag.text}
	  		</div>
	  	)
		});
		return tagsJSX;
	};

  return (
	  <main>
	  	<section className={style.hero}>
	  		<div className={style.headSide}>
	  			<h1>{lang.header[0]} <br/> {lang.header[1]} <span>{lang.header[2]}</span></h1>
	  		</div>
	  		<div className={style.textSide}>
	  			<p>{lang.desc}</p>
	  			<Link to={`${host}about`} className={style.button}>{lang.buttonMore}</Link>
	  		</div>
	  	</section>

	  	<section className={style.filters}>
	  		<div onClick={() => setOpenCountry(!openCountry)} className={`${style.filter} ${openCountry ? style.active : ''}`}>
	  			{lang.filterCountry[country]}
	  			<div className={style.filterList}>
	  				{filterList()}
	  			</div>
	  		</div>
	  		<div onClick={() => setOpenAction(!openAction)} className={`${style.filter} ${openAction ? style.active : ''}`}>
	  			{lang.filterAction[action]}
	  			<div className={style.filterList}>
	  				{actionList()}
	  			</div>
	  		</div>
	  		{
	  			tags && tags.length ? (
			  		<div onClick={clickTagFilter} className={`${style.filter} tags ${openTags ? style.active : ''}`}>
			  			{lang.tags} {calcCheckedTags()}
			  			<div className={`${style.filterList} ${style.tagList}`}>
			  				{tagsList()}
			  			</div>
			  		</div>
	  			) : (
	  				undefined
	  			)
	  		}
  			<div className={style.search}>
  				<input onFocus={closeFilters} value={searchString} placeholder={lang.search} type="text" onChange={search}/>
  			</div>
	  	</section>
	  	<CardsWrapper 
	  		empty={lang.empty}
	  		cards={cards}
	  		more={lang.buttonMore}
	  		contacts={lang.buttonContacts}
	  		country={country}
	  		action={action}
	  	/>
	  </main>
  );
}

export default Home;
