const find = function(arr, find) {
  return arr.find(function (value) {
     return value === find;
  });
};


const uniqueId = function(arr) {
  let result = [];
  let resultIds = [];
  for (let obj of arr) {
    if (!find(resultIds,obj.id)) {
    	resultIds.push(obj.id);
      result.push(obj);
    }
  }
  return result;
}

export { uniqueId, find }