import style from './index.module.css';
import { content } from './content.js'
import { Link, useLocation } from "react-router-dom";
import { useState, useEffect } from 'react';


function Header(props) {
	const lang = content[props.lang]
	const [open, setOpen] = useState(false);
	const [openMobile, setOpenMobile] = useState(false);
	const path = useLocation().pathname;
	const host = window.env.ENV_HOST;

	const page = (path) => {
		if (path.includes(`${host}card`) || (path === host)) return 'home';
		if (path.includes(`${host}org`) || (path === host)) return 'org';
		if (path === `${host}faq`) return 'faq';
		if (path === `${host}about`) return 'about';
		if (path === `${host}contacts`) return 'contacts';
	};

	const navLinks = (
		<>
		  <Link to={host} className={`${style.link} ${page(path) === 'home' ? style.active : ''}`}>{lang.requests}</Link>
		  <Link to={`${host}org`} className={`${style.link} ${page(path) === 'org' ? style.active  : ''}`}>{lang.org}</Link>
  		<Link to={`${host}faq`} className={`${style.link} ${page(path) === 'faq' ? style.active  : ''}`}>{lang.faq}</Link>
  		<Link to={`${host}about`} className={`${style.link} ${page(path) === 'about' ? style.active  : ''}`}>{lang.about}</Link>
  		<Link to={`${host}contacts`} className={`${style.link} ${page(path) === 'contacts' ? style.active  : ''}`}>{lang.contacts}</Link>
  	</>
	)
	const switchContent = (
		<>
  		{lang.langActive}
  		<div className={`${style.switchList} ${open ? style.open : ''}`}>
  			<div onClick={() => props.setLang(content.langs[0].id)}>{content.langs[0].lang}</div>
  			<div onClick={() => props.setLang(content.langs[1].id)}>{content.langs[1].lang}</div>
  			<div onClick={() => props.setLang(content.langs[2].id)}>{content.langs[2].lang}</div>
  		</div>
  	</>
	)

  return (
	  <header className={style.header}>
	  	<Link to={host}><div className={style.logo}></div></Link>
	  	<div className={`${style.link} ${style.active}`}>DEMO</div>
	  	<div className={`${style.nav} ${style.desktop}`}>
	  		{navLinks}
	  	</div>
	  	<div className={`${style.switch} ${style.desktop}`} onClick={() => setOpen(!open)} >
	  		{switchContent}
	  	</div>
	  	<div className={`${style.mobileButton} ${style.mobile}`} onClick={() => setOpenMobile(!openMobile)}></div>
	  	<div className={`${style.nav} ${style.mobile} ${openMobile ? style.open : ''}`}>
	  		<div className={style.closeIcon} onClick={() => setOpenMobile(!openMobile)}>
	  			<span></span><span></span>
	  		</div>
	  			{navLinks}
		  	<div className={style.switch} onClick={() => setOpen(!open)} >
		  		{switchContent}
		  	</div>
	  	</div>
	  </header>
  );
}

export default Header;