import style from "./index.module.css";


function TextBox(props) {
	const header = props.header;
	const text = props.text;

	return(
		<>
			<h2 className={style.header}>{header}</h2>
			{
				text.map((item,i)=>(
					<div key={i} className={style.paragraph}>
						{item}
					</div>
				))
			}
  	</>
	)
}

export default TextBox;
