import { content } from './content.js';
import style from './index.module.css';

function Recommended(props) {
  const host = window.env.ENV_HOST;
  const lang = content[props.lang];

  return (

    <div className={style.donate}>
      <div className={style.donateBox}>
        <h3>{lang.isSupport}</h3>
        <p>{lang.isSupportText}</p>
      </div>
      <div className={style.donateList}>
        {
          lang.info.link.map((link) => (
            <div key={link.head} className={style.donateList__box}>
              <span className={style.donateItem__head}>{link.head}</span>
              <span className={style.donateItem__item}>
                <input className={style.donateItem__text} readOnly value={link.content}/>
                <a className={`${style.donateItem__icon} ${style.link_icon}`} target="_blank" href={`${host}${link.content}`}></a>
              </span>
            </div>
          ))
        }
      </div>
    </div>
  )
}

export default Recommended;