import './App.css';

import { BrowserRouter, Routes, Route } from "react-router-dom";
import { useState, useEffect } from 'react';


import Header from './components/header';
import Footer from './components/footer';
import Home from './home';
import Card from './card';
import FAQ from './faq';
import AboutUs from './about';
import ContactUs from './contacts';
import Top from './top';


window.env = {
  ENV_HOST: '/donationsvsevil/',
};

function App() {
	const [country, setCountry] = useState("ukraine");
	const [action, setAction] = useState("salvation");
	const [lang, setLang] = useState("ukr");
	const userLang = navigator.language || navigator.userLanguage;
  useEffect(() => {
		if (userLang.includes('ru')) {setLang('rus')};
		if (userLang.includes('en')) {setLang('eng')};
  }, [userLang]);

  const host = window.env.ENV_HOST;

  return (
    <BrowserRouter>
    	<Header lang={lang} setLang={setLang} />
	    <Routes>
        <Route path={`${host}card/:top/:country/:action/:id/`} element={<Card lang={lang} />}/>
	      <Route path={`${host}org/card/:country/:action/:id/`} element={<Card lang={lang} />}/>
	      <Route path={`${host}faq`} element={<FAQ lang={lang} />} />
	      <Route path={`${host}about`}  element={<AboutUs lang={lang} />} />
	      <Route path={`${host}contacts`} element={<ContactUs lang={lang} />} />
	      <Route path={`${host}org`} element={<Home lang={lang} country={country} setCountry={setCountry} action={action} setAction={setAction} />}	/>
	      <Route path="*" element={<Top lang={lang} country={country} setCountry={setCountry} action={action} setAction={setAction} />}	/>
	    </Routes>
	    <Footer lang={lang} setLang={setLang} />
    </BrowserRouter>
  );
}

export default App;
